.MenuList{
  max-width: 900px; margin:50px auto;
  h1{
    text-align:center;
    margin-bottom: 20px;
  }
  ul{ background: #ddd;
    li{
        float: left;
        width: 25%;
        text-align: center;
        border:1px solid #fff;
            img{width:100%; opacity :0.8; transition:all 0.5s;
                &:hover{opacity:1}
            }
            span{display: block; margin:10px 0;}
      }
  }
}
